@import "../../../../css-config/mixins.scss";
.html-side-menuv {
   
    border-right: 1px solid rgba(0, 0, 0, 0.3);
    overflow: hidden;
   

    .html-menu-listv {
       
        ul {
            


            li {
               
                display: flex;
                list-style: none;
                padding: 8px 16px;
                border-bottom: 1px solid rgba(0, 0, 0, 0.1);
                cursor: pointer;
                pointer-events:visible;
                transition: 0.3s all;
                
                // @include ssms {
                //  pointer-events:none;
                // }
                
              

               

                &:hover {
                    background-color: black;
                    color: aqua;
                    cursor: pointer;
                  
                    
                    


                }

            }

        }
    }
}
