@import "../../../css-config/mixins.scss";

.Main-info{
    margin: 60px 0;
    width: 100%;
  
  
    .Info-pages{
        display: flex;
        justify-content: center;
        flex-flow: wrap;
        gap: 15px;
        padding: 0px 5px;
       
        
    }
    .desc-box{
       
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 10px;
        padding: 10px;
        height: 200px;
        width: 300px;
        padding: 0 15px;
        transition: 0.7s ease-in-out;
            background-color: rgb(255,162,170);
            border-radius: 30px;
       

        @include md{
            background: rgb(230, 227, 227);
            border-radius: 0;

        }


      
        
        &:hover{
            transition: 0.7s ease-in-out;
            background-color: rgb(255,162,170);
            border-radius: 30px;
            // cursor: pointer;
        }

        svg{
            font-size: 50px;
            
        }
        p{
            text-align: justify;
            color: rgba(0,0,0,0.8);
           
        }
    }
    .desc-box2{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-flow: wrap;
        gap: 15px;
    }
}