    .html-study {
        width: 100%;
        height: 100vh;
        background: rgb(255, 255, 255);
        // position: fixed;
        left: 20%;
        z-index: 2;
        // display: flex;
        // flex-direction: column;
        // justify-content: center;
        overflow-y: scroll;
        padding: 10px 20px;
        background-color: rgb(216, 216, 216);
       
       

        @include md {
            width: 80%;
            padding: 40px 60px;

        }

        .menu-oppen{
            margin-top: 20px;
            background-color: white;
            color: black;
            padding: 8px 12px;
            width: fit-content;
            border-bottom: 3px solid green;
            position: fixed;

            @include md{
                display: none;
            }

           

          
        }


       


       


      

        .html-title-desc{
            text-align: justify;
            letter-spacing: 1px;
            
            font-size: 16px;
            line-height: 29px;           
            margin: 40px auto;
            word-spacing: 6px;
            // text-transform: capitalize;
            color: rgba(0, 0, 0, 0.8);
            display: flex;
            flex-direction: column;
            gap: 100px;

            .html-title-heading{

                font-size: 34px;
                letter-spacing: 2px;
                word-spacing: 6px;
                font-weight: 600;
                margin-top: 60px;
    
                &:after {
                    content: "";
                    display: block;
                    margin-top: 5px;
                    width: 50px;
                    height: 3px;
                    background-color: #8e2de2;
        
                    @include md {
                        margin-top: 10px;
                    }
                }
    
    
               
            }

            .html-title2-heading{

                font-size: 34px;
                letter-spacing: 2px;
                word-spacing: 6px;
                font-weight: 600;
                // margin-top: 60px;
                display: none;
    
                &:after {
                    content: "";
                    display: block;
                    margin-top: 5px;
                    width: 50px;
                    height: 3px;
                    background-color: #8e2de2;
        
                    @include md {
                        margin-top: 10px;
                    }
                }
    
    
               
            }

            img{
                display: none;
                width: calc(100% - 50px);
                // background-color: #8e2de2;
             
               
            }

            .html-desc2{
               border: 1px solid black;
               border-right: 10px solid rgb(125, 11, 255);
               padding: 20px 30px;
               overflow: auto;
            }

            .html-desc3{
                border: 1px solid rgba(0, 0, 0, 0.5);
                // border-right: 10px solid rgb(125, 11, 255);
                background-color: white;
                padding: 20px 30px;
                word-wrap: break-word;
                overflow: auto;
                line-height: 40px;
             }

            

        }

        


        // .react-image-intro{
        //     min-width: calc(100% - 20%);
