        // }

        .html-question-answer{
            text-align: justify;
            font-size: 16px;
            line-height: 45px;  
            margin-top: 60px;       
           
            word-spacing: 6px;
           
            
           

            .html-question{
                font-size: 29px;
                color: blue;
                text-transform: capitalize; 

                &:after {
                    content: "";
                    display: block;
                    margin-top: 5px;
                    width: 50px;
                    height: 3px;
                    background-color: #8e2de2;
        
                    @include md {
                        margin-top: 10px;
                    }
                }
            }

            .html-output{
                font-size: 29px;
                color: blue;
                margin: 30px auto;
                text-transform: capitalize; 

                &:after {
                    content: "";
                    display: block;
                    margin-top: 5px;
                    width: 50px;
                    height: 3px;
                    background-color: #8e2de2;
        
                    @include md {
                        margin-top: 10px;
                    }
                }
            }

            .html-answer{
                // background-color: rgba(0, 0, 0, 0.87);
                // border: 1px solid rgba(0, 0, 0, 0.5);
            color: rgb(3, 3, 3);
            margin-top: 40px;
            overflow: scroll;
            padding: 20px;
            font-size: 18px;
            letter-spacing: 2px;
            font-weight: 0;
            width: auto;
            border-top: 1px solid rgba(0, 0, 0, 0.514);
            border-left:10px solid rgb(5, 38, 254);
            
         
           

            

            }

          

        }

        
     


