@import '../../../../css-config/mixins.scss';

.gallery{
  margin-top: 60px;
  margin-bottom: 60px;
  -webkit-column-count: 3;
  -moz-column-count: 3;
  column-count: 3;
  -webkit-column-width: 33%;
  -moz-column-width: 33%;
  column-width: 33%;
  padding: 20px 12px;
  border-top:1px solid gray;
  border-bottom:1px solid gray;
 

  @include me{
    border-bottom: none;
    padding-bottom:0;
  }

  .pics{
    -webkit-transition: all 350ms ease;
    transition: all 350ms ease;
    margin-bottom: 12px;
    
    &:hover{
      cursor: pointer;
      transform: scale(.9);
  
    }
  }

}