@import '../../../../css-config/mixins.scss';

.certification {
    // background-image: url("../../../../assets/newsletter-bg.jpeg");

    margin: 80px 0px;
    
    .certificate-box {

        display: flex;
        justify-content: center;
        align-items: center;
      flex-flow: wrap;
      gap: 90px;


        @include me{

            gap: 60px;
        }
        // @include mml{
        //     gap: 80px;
        // }
    
        // @include sms{
        //     gap: 130px;
        // }


        .certificate {

            img {
                box-shadow: 5px 5px 5px 5px lightblue;
                height: 200px;
                width: auto;

                // @include me{
                //     height: 550px;
                    
                // }
                @include s{
                   height: 300px;
                   width: auto;
                }

                @include md{
                    height: 400px;
                    width: auto;
                }
            }

        }

        .certificate-content {


            li {
                margin: 12px;
                gap: 5px;
                display: flex;
                font-size: 18px;
                list-style: none;

                @include s{
                    font-size: 20px;
                }

                svg {
                    color: green;
                }
            }
        }

    }
}