@import "../../../css-config/mixins.scss";

.categories-slider{
    padding: 20px 20px 20px 20px ;
    .react-multiple-carousel__arrow {
        position: absolute;
        outline: 0;
        transition: all .5s;
        border-radius: 35px;
        z-index: 1;
        border: 0;
        background: rgba(0, 0, 0, 0.5);
        min-width: 43px;
        min-height: 43px;
        opacity: 1; 
        cursor: pointer
    }


 

 

   
    

 

   
    


