@import "../../../css-config/mixins.scss";

.collage-main{
    padding: 0px 20px 20px 20px;
    @include md{
        padding: 20px 20px 20px 20px;
    }
    // display: flex;
    // justify-content: center;
    .react-multiple-carousel__arrow {
        position: absolute;
        outline: 0;
        transition: all .5s;
        border-radius: 35px;
        z-index: 1;
        border: 0;
        background: rgba(0, 0, 0, 0.5);
        min-width: 43px;
        min-height: 43px;
        opacity: 1; 
        cursor: pointer
    }

    .slider-student{
        text-align: center;
        padding-bottom: 30px;
        font-size: 17px;
        color: rgba(0, 0, 0, 0.5);
        font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;

        @include md{
            padding-bottom: 40px;
            font-size: 28px;
        }
    }

    .collage-contain{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 10px;



        .col-img{
            height: 80px;
            width: 80px;
            @include md{
                height: 170px;
                width: 170px;
            }

        }

        .col-name{
            color: blue;
            font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
            font-size: 15px;
            text-align: justify;
        }
    }


 

 

   
    

 

   
    


  
        
  
   

