@import '../../../../css-config/mixins.scss';

.Indus-main {

    padding: 40px;
    background-image: url('./bg1.jpeg');
    background-size: cover;
    color: white;
    height: 600px;
        @include xxs{
            padding: 20px;
         
        }


        .Indus-heading {
            line-height: 50px;
            margin-top: 50px;
            text-align: center;

            @include xxs{
                font-size: 20px;
            }
        }

        .Indus-desc {
            text-align: center;
            margin-top: 40px;

        }

        .Industry {
    
            position: relative;
            top: 120px;
        
        
            .Indus-element{
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 70px;
                margin-bottom: 60px;

                @include md{
                    font-size: 18px;
                }

                @include ss{
                    gap: 40px;
                    font-size: 14px;
                }
                @include xxs{
                    gap: 35px;
                    font-size: 12px;
                }
            
            svg {
                position: relative;
                left: 30%;
                
            }
        }
        }

    

   
}