@import "../../css-config/mixins.scss";

.main-header{
    width: 100%;
    padding: 0 20px;
    background-color: black;
    color: white;
    border-bottom: 1px solid rgba(0,0,0,0.1);
    z-index: 99; 
    
    @include md{
        padding: 0 40px;
    }
    
    .header-content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        max-width: 1200px;
        margin: 0 auto;
        @include md {
            height: 80px;
        }
        .left{
            list-style-type: none;
            color: grey;
            display: none;
            gap:25px;
            text-decoration: none;
            @include md{
                display: flex;
                

            }

       

            .icon-part{
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 5px;
                flex-direction: column;
                transition: all ease-in;
                animation: 0.2s;
                color: grey;

                @include md{
                    &:hover{
                        transform: scale(0.9);
                        color: aqua;
                    }
                }

               
            }
            li{
                font-size: 14px;
                font-weight: 600;
                text-transform:uppercase;
                cursor: pointer;

            };
           


        }

        .center{
            font-size: 22px;
            color: rgb(0, 217, 255);
            font-weight: 700;
            cursor: pointer;
            @include md{
                font-size: 34px;
                position: absolute;
                color: rgb(0, 225, 255);
                left: 50%;
                transform: translateX(-50%);
            }

        }

        .right{
            display: flex;
            align-items: center;
            gap: 20px;
            @include md{
                gap:25px
            }

            svg{
                font-size: 20px;
                cursor: pointer;
                font-weight: 700;
                color: grey;
                transition: all ease-in;
                animation: 0.2s;
               
                @include md{
                    font-size: 24px;

                    &:hover{
                        transform: scale(1.13);
                        color: aqua;
                    }
                }
            }
            .cart-icon{
                position: relative;
                span{
                    min-width: 20px;
                    text-align: center;
                    background-color: #8e2de2;
                    padding: 2.5px;
                    position: absolute;
                    top: -5px;
                    right: -12px;
                    font-size: 12px;
                    line-height: 1;
                    border-radius: 10px;
                }
            }

        }
    }
    a{
        text-decoration: none;
        color: white;
    }

    &.sticky-header{
        position: sticky;
        top: 0;
       
        animation: stickyHeader 0.3s ease forwards;
        width: 100%;
        padding: 0 20px;
        background-color: #ffffffe6;
        color: white ;  
        border-bottom: 1px solid rgba(0,0,0,0.1);
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        z-index: 99; 

       
        
    }
}

@keyframes stickyHeader{
    0% {transform:translateY(-80px);}
    100%{transform: translateY(0);}
}