@import "../../../css-config/mixins.scss";

.main-contact {
    background-color: rgb(0, 174, 255);
    display: flex;
    justify-content: center;
    align-items: center;

    .contact {
        margin: 20px;
        padding: 26px 0;
        max-height: fit-content;
        width: 1000px;
        border-radius: 20px;
        background-color: #053da0;
        color: white;

        .heading {
            padding: 20px 0;
            text-align: center;

            span {
                position: relative;
                top: 13px
            }
        }

        .content {


            margin: 50px;
            display: flex;
            flex-flow: wrap;
            justify-content: center;
            align-items: center;
            gap: 60px;
            font-size: 18px;
            
            cursor: pointer;

            .sub-content {
                padding: 8px 31px;



               
               


            }

            #mail{
               
                    background-color: rgb(5, 118, 188);
                    border-radius: 20px;
        
             
            }
           


        }
       

        .page{
            display: flex;
            justify-content: center;
            justify-content: center;
            
        }

    }

   
}