@import "../css-config/mixins.scss";

.fixed-batch{
    position: fixed;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    color: rgb(7, 2, 0);
    width: 100px;
    letter-spacing: 1PX;
    padding: 10px 16px;
    z-index: 1;
    font-weight: 700;
    // text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.352), 0 0 25px rgba(10, 253, 91, 0.434), 0 0 5px darkblue;
    // writing-mode: vertical-rl;
    border-radius: 20px 0 0 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
   text-align: center;
    flex-direction: column-reverse;
    overflow: hidden;
gap: 5px;
    
background-color: white;
  transition: width 0.3s;

  @include md{
    flex-direction: column-reverse;
    gap: 20px;
    width: 50px;
    writing-mode: vertical-rl;



    &:hover{
        width: 170px;
        flex-direction: column-reverse;
       
    }

   

    
    &:hover img{
        display: block;
       
        }
    
    }


    img{
        
        display: block;
        height: 60px;
        width: 70px;

        @include md{
            display: none;
            height: 100px;
            width: 100px;

          
        }
       

       
    }
        
   

  }

     
   

    // text-orientation: upright;

   

