@import "../../../css-config/mixins.scss";
.main-service-page {
    flex-flow: wrap;
    margin: 60px 0px;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;

    .main-page {
        padding: 2px 17px;
        width: 210px;
        height: 350px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 30px;
        flex-direction: column;
        
        border: 1px solid black;
       


        @include md{
          
            &:hover .page-image{
                border: 3px solid rgb(2, 77, 253);
               
               
            }
        }
       

        @include  cardf{
            width: 300px;
        }



        .page-image {

            position: relative;
            border-radius: 100%;
           
            height: 122px;
            width: 122px;
            border: 3px solid rgb(2, 77, 253);

            @include md{
                border: 2px dashed black;
            }

            img {
                top: 18px;
                left: 18px;
                position: absolute;
                height: 80px;
                width: 80px;
            }
           
        }

        p {
            text-align: justify;
          
        }
    }
}