@import "../../../css-config/mixins.scss";


.project-main{

  margin: 40px 0;
  padding: 0 10px;

  .title-project{
    font-size: 20px;
    line-height: 32px;
    margin-bottom: 20px;
    text-transform: uppercase;
    word-spacing: 8px;
    font-weight: 700;

    @include md{
      margin: 40px;
    }


    &:after {
        content: "";
        display: block;
        margin-top: 5px;
        width: 50px;
        height: 3px;
        background-color: #8e2de2;

        @include md {
            margin-top: 10px;
        }
    }

  
  }

.containerm{
  display: grid;
  grid-template-columns: auto;
  gap: 30px;

  @include mdd{
    grid-template-columns: auto auto auto;
    gap: 10px;

  }

  // @include cardl{
  //   grid-template-columns: auto auto;
  //   gap: 20px;

  // }


  .card-main{
    width: 100%;
    margin: 0 auto;
   
    
    @include md{
      width: 80%;
    }
 
  

   
 


  .card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
  height: 350px;
    // margin: 0 auto;
    position: relative;
    cursor: pointer;

    &:hover .overlay{
      height: 100%;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      
    }
    
    // &:hover {
    //   box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    // }

    // &:hover .overlay {
    //   height: 100%;
    // }


    .overlay {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: #0003ba34;
      overflow: hidden;
      width: 100%;
     height: 100%;
      transition: .5s ease;


      @include md{
        height: 0;
      }


      .text {
        color: white;
        font-size: 20px;
        position: absolute;
        overflow: hidden;
        top: 10%;
        left: 10%;
        font-weight: 900;
        transform: translate(-10%, -10%);
        -ms-transform: translate(-50%, -50%);
        white-space: wrap;
        line-height: 30px;
        padding: 0 10px;
      }

      .demo{
        white-space: nowrap; 
        color: white;
        font-size: 20px;
        position: absolute;
        overflow: hidden;
        top: 90%;
        left: 90%;
        font-weight: 900;
        transform: translate(-90%, -90%);
        // -ms-transform: translate(-50%, -50%);
        white-space: wrap;
        line-height: 30px;
        background-color: blue;
        padding: 8px 24px;
        border-bottom: 2px solid white;
        transition: 0.2s all;

        @include md{
          padding: 12px 16px;
        background-color: green;
        border: none;
        }
       

        &:hover{
          background-color: blue;
          padding: 8px 24px;
          border-bottom: 2px solid white;
        }
      }
      
    }
  
  }


  .container {
    padding: 12px 16px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    &:hover .overlay{
      height: 100%;
    }
   
    h4{
      line-height: 30px;
      white-space: wrap;
      text-transform: uppercase;
      color: rgba(0,0,0,0.5);
    }

    p{
      line-height: 30px;
      white-space: wrap;
      font-size: 15px;
      color: rgba(0, 0, 0, 0.689);

    }
  }
  

}






}

}
    















