@import "../../../..//../../css-config/mixins.scss";

.single-product-main-content {
    margin: 40px 0;
   

    @include md {
        margin: 55px 0;
    }

   



    .layout {
        max-width: calc(100% - 20px);
        margin: 0 auto;

        @include md {
            max-width: 1200px;
        }

        .title {
            text-align: center;
            font-size: 24px;
            text-transform: capitalize;
            font-weight: 900;
          
    
            @include md {
                font-size: 34px;
                text-transform: capitalize;
    
            }

           
    
    
    
    
        }


        .single-product-page {
            margin-top: 20px;
            display: flex;
            flex-direction: column;

            align-items: center;


            @include mdd {
                flex-direction: row;
                margin-top: 80px;
            }

            .leftp {
                width: 100%;
                // background-color: rgba(0, 0, 0, 0.05);
                flex-shrink: 0;
                // transform-style: preserve-3d;
                // box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
                

                // box-shadow: 1px 1px 1px 1px;
                @include md {
                    width: 500px;
                    height: 300px;
                }

                img {
                    width: 100%;
                    display: block;
                    height: 100%;
                    // box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                }


            }

            .right {
                display: flex;
                flex-direction: column;
                padding-top: 20px;

                @include md {
                    padding: 0 55px;

                    // margin-top: 90px;

                }


                .price {
                    font-size: 20px;
                    line-height: 32px;
                    margin-bottom: 20px;
                    text-transform: uppercase;
                    word-spacing: 8px;
                    font-weight: 700;

                    .buttonf{
                      
                      width: 140px;
                      font-size: 12px;

                      @include md{
                        font-weight: 600;
                        width: 160px;

                      }

                        svg{
                            font-size: 19px;
                        }
                    }


                    &:after {
                        content: "";
                        display: none;
                        margin-top: 5px;
                        width: 50px;
                        height: 3px;
                        background-color: #8e2de2;

                        @include md {
                            display: block;
                            margin-top: 10px;
                        }
                    }



                }

                .desc {
                    text-indent: 50px;
                    text-align: justify;
                    font-size: 14px;
                    line-height: 20px;
                    margin-bottom: 20px;
                    color: #6b6b6b;
                    padding:0 10px ;

                    @include md {
                        font-size: 16px;
                        line-height: 30px;
                        
                    }
                }









            }
        }
    
    
 
    }
}

@keyframes example {

    0% {
        left: 0px;
        top: 0x;
    }

    100% {
        left: 50%px;
        top: 0px;
    }
}